export default {
	data() {
		return {
			
			img_object:[{}],
			uid: 1,
			title:"习惯",
			index: 0,
			array: ['起床', '晨间', '午间', '晚间', '睡前'],
			check_time:7,
			check_flag: true,
			checked_text: '需要提醒',
			img: [
				'../../static/png/1.png',
				'../../static/png/2.png',
				'../../static/png/3.png',
				'../../static/png/4.png',
				'../../static/png/5.png',
				'../../static/png/6.png',
				'../../static/png/7.png',
				'../../static/png/8.png',
				'../../static/png/9.png',
				'../../static/png/10.png',
			],
			img_id: 0,
		}
	},

	onLoad: function(e) {
		this.input_tap=e.title,
		this.check_flag = e.check_flag,
		this.img_id = e.img_id
	},

	methods: {
		/*提醒选择*/
		set_clock() {
			this.checked_text = "不需要提醒",
				this.check_flag = false;
		},
		/*图片点击选择*/
		click_img(e) {
			this.img_object = e.img_object;
		},
		/*获取新标题*/
		get_name: function(event) {
			this.title = event.target.value
		},
		/*修改打卡申请*/
		push_button() {
			uni.request({
				url: 'http://localhost/api/habit/revise',
				method: 'GET',
				data: {
					uid: this.uid,
					title: this.title,
					remind: this.check_flag,
					img: this.img_id,
				},
				success: function(e) {
					console.log(e)
				}
			})
		}
	}
}
